        size: '10px', //
        color: '#000', //
        position: 'right', //left/right
        distance: '0px', //
        start: 'top', //top/bottom
        opacity: .2, //
        alwaysVisible: true, // 
        disableFadeOut: true, // 
        railVisible: true, // 
        railColor: '#333', //
        railOpacity: .1, //
        railDraggable: true, // 
