          this.$message.error(this.$t('rvcpinfo.nosellect'));
        }else{
          console.log(this.activeTableName);
        }
      },/**/
    }}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main{
    color: #3e3e3e;
