<template>
  <el-container class="main">
    <el-row id="aside" class="aside">
      <router-link v-for="(item,index) in asideArray" :to="{ name:item.name}" class="asideItem">
        <i :class="item.className"></i>
        <span>{{$t(item.textCon)}}</span>
      </router-link>
    </el-row>
    <el-row class="content">
      <router-view></router-view>
    </el-row>
  </el-container>
</template>
<script>
  import  router from '../router/index'
  export default {
    data () {   /*datadata()data*/
      return {
        title:'Report Builder',
        asideArray:[
          {name:'nav5_1',className:'asideItem01',textCon:'rvcpaside.nav5_1'},
          {name:'nav5_2',className:'asideItem02',textCon:'rvcpaside.nav5_2'},
