        height: '100%', //
        size: '5px', //
        color: '#000', //
        position: 'right', //left/right
        distance: '0px', //
        start: 'top', //top/bottom
        opacity: .2, //
        alwaysVisible: true, // 
        disableFadeOut: false, // 
        railVisible: true, // 
        railColor: '#333', //
        railOpacity: .1, //
        railDraggable: true, // 
        railClass: 'slimScrollRail', //div
        barClass: 'slimScrollBar', //div
        wrapperClass: 'slimScrollDiv', //div
        allowPageScroll: true, // /
        wheelStep: 10, //
        touchScrollStep: 200, //
        borderRadius: '7px', //
        railBorderRadius: '7px' //
      });//
    },//
    watch: {
      '$route' (to, from) {
        //
        this.getStatus(this.$route.path)
      }
    },
    methods: {
      getStatus (urlStr) {
        var urlStrArr = urlStr.split('/')
        return urlStrArr[urlStrArr.length - 1]
      },
      getPagedata:function () {
        var thisVue = this;
        thisVue.ajax({
          method: 'post',
          /*         url:thisVue.localhost,*/
          headers:{
            /* "Conten-Type":thisVue.localhost*/
          },
          data: {
          }
        })
          .then(function (response) {
          })
          .catch(function (error) {
            console.log(error);
          });

      },//
    }}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main{
    height:calc(100vh - 60px);
    width:100%;
    margin:0 auto;
    text-align: center;
    background-color: #f6f6f6;
  }
  .aside{
    background-color: #ffffff;
    width: 120px!important;
    min-width: 120px!important;
    height: calc(100vh - 60px);
    overflow: auto;
    padding: 10px  0;
    color: #888888;
    border:#d2d2d2 solid 1px;
    border-radius: 4px;
    border-right: 0;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: flex-end;
    background-color: #ffffff;
  }
  .content{
    width: calc( 100% - 120px  )!important;
  }
  .asideItem{
    width:100px;
    min-width:100px;
    height:90px;
    min-height:90px;
    margin:0 5px 0 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    color: #606061;
  }
  .asideItem:hover{
    background-color: #ffffff;
    color: #606061;
  }
  .asideItem01{
    background: url("../../../static/img/ico/icon-5.1.png") no-repeat;
    width:25px;
    height:30px;
  }
  .asideItem02{
    background: url("../../../static/img/ico/icon-5.2.png") no-repeat;
    width:25px;
    height:30px;
  }
  .asideItem03{
    background: url("../../../static/img/ico/icon-5.3.png") no-repeat;
    width:25px;
    height:30px;
  }
  .asideItem04{
    background: url("../../../static/img/ico/icon-5.4.png") no-repeat;
    width:25px;
    height:30px;
  }
  .router-link-active{
    background-color: #f3f3f5!important;
    border: #d2d2d2 solid 1px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    border-right: 0px;
  }
</style>



